import {LoginOut} from 'api/user'
export default {
	data() {
		return {
			userId:''
		}
	},
	onLoad() {
		this.$store.ready(state=>{
			this.userId = state.userId
		})
	},
	onShow(){

	},
	methods: {
		open(url){
			uni.navigateTo({
				url:url
			})
		},
		loginOut(){
			uni.showModal({
			    title: '提示',
			    content: '确定要退出吗？',
			    success: res=> {
			        if (res.confirm) {
						this.$store.dispatch('loginout').then(res=>{
							uni.switchTab({
								url:'/pages/user/index'
							})
						})
			            
			        }
			    }
			});
			
			
		}
		
	}
}
